<template>
  <div style="overflow: hidden">
    <div v-if="!isaffrim">
      <div class="mhead" style="height: 8%;padding-top: 2%;">
        <ul>
          <li>
            <text style="position: absolute;right: 0px">查询用户</text>
           </li>
        </ul>
      </div>
      <div class="searchview auto">
        <div class="form-group row auto" style="margin-top:10px;">
          <div class="col-xs-9">
            <label  class="col-xs-4 control-label" style="font-size:18px" ><nobr>缴费编号</nobr></label>
            <div class="col-xs-7">
              <input type="number" class="form-control"  v-model="f_userinfo_id"
                     condition="f_userid = {} " placeholder="请输入用户编号"/>
            </div>
          </div>
          <div class="col-xs-3" >
            <button  type="button" class="btn btn-sm btn-primary"  @click="search()"> 查询 </button>
          </div>
        </div>
        <div style="height: 5%;">
          <table class="table table-bordered self-table">
            <thead style="background-color: #499edf">
              <tr class="table-tr-1">
                <th style="text-align:center;"><nobr>用户名称</nobr></th>
                <th style="text-align:center;"><nobr>用户地址</nobr></th>
                <th style="text-align:center;"><nobr>操作</nobr></th>
              </tr>
            </thead>
            <tbody v-for = "row in model.rows">
              <tr>
                <td style="text-align:center;"><p class="name">{{row.f_user_name.substring(0,1)+'**'}}</p></td>
                <td style="text-align:center;"><p class="address">{{row.f_address.substring(0,6)+'**'}}</p></td>
                <td style="text-align:center;">
                  <button type="button" class="btn btn-primary btn-sm"   @click='affrim(row)'>确认</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="foot" style="margin-top: 124%;" @click="back('tomain')">
          <text>返回</text>
        </div>
      </div>
    </div>
    <user-info v-if="isaffrim" :userinfo="userInfo" :cardinfo="cardInfo"></user-info>
  </div>
</template>
<script>
  import Vue from "vue";
  let getuser = function* (self) {
    let request = {
      condition : '1=1 and  f_userinfo_code=\''+self.f_userinfo_id+'\' order by f_userinfo_code desc'
    }
    let result = yield self.$resetpost(Vue.ProxyUrl + '/rs/sql/'+Vue.PosUtil.posParam.revenue+'getUserInfo', {data: request}, {resolveMsg: null, rejectMsg: null})
    if (result){
      if (result.data.length === 0){
        self.$showMessage("未查到记录，请您确认您输入的用户编号！")
      }
      self.model.rows = result.data
    }
  }
  import co from 'co'
  export default {
    title: '用户查询',
    data () {
      return {
        isaffrim: false,
        userInfo: {},
        cardInfo: {},
        f_userinfo_id:'',
        model: {rows: []},
      }
    },
    props: ['sellType','cardData'],
    methods: {
        search(){
           if(this.f_userinfo_id === ''){
               this.$showAlert('请先输入用户编号', 'warning', 1500)
               return
           }
          this.model.rows = []
          co(getuser(this))
        },
      back (param) {
        this.$back(param)
      },
      callBack(param){
          if(param === "toCheck"){
            return
          }
          this.$back(param)
      },
      affrim (row) {
        // 判断用户信息和卡信息是否一致
        console.log('卡上信息：', this.cardData)
        if (this.cardData.CardID === row.f_card_id && this.cardData.Factory === row.f_alias){
          this.$goto('user-info',{userInfo:row,sellType:this.sellType},'self',this.callBack)
        }else{
          this.$showMessage('您的用户信息和卡信息不匹配！', ['confirm'])
        }
      },
    },
    events: {

    },
    ready () {

    }
  }
</script>



